$(function(){
  // 设置视频高度为浏览器的高度
  $('.in-video').height($('.in-video').height());
  $('body').removeClass('hp100 ovh');
  var winWidth = $(window).width();
  var isPC = winWidth>750;
  var myPlayer = videojs('player-Abbreviated');
  var pause =  function(){ // 暂停
    $('#palybut').show();
    myPlayer.pause();
  }
  var play =  function(){ //播放
    $('#video_cover').hide();
    $('#palybut').hide();
    myPlayer.play();
  }
  if(isPC){
    videojs("player-Abbreviated").ready(function(){
      play()
   });
  }
  $('.video-mask').on('click',function(){
    pause();
    return false;
  })
  $('#palybut').on('click',function(){
    // play();
    $('#myModal').modal('show');
    return false;
  })
  var modalPlayer = null;
  $('#myModal').on('show.bs.modal', function (e) {
    if(!modalPlayer){
      modalPlayer = videojs('myModal-Abbreviated');
    }
    videojs("myModal-Abbreviated").ready(function(){
      modalPlayer.play();
    });
  })
  $('#myModal').on('hidden.bs.modal', function (e) {
    modalPlayer.pause();
  })
  
  // 通告走马灯效果
  if(isPC){
    $(".txtMarquee-left").slide({mainCell:".bd ul",autoPlay:true,effect:"leftMarquee",vis:2,interTime:50});
  }else{
    $(".txtMarquee-left").slide({mainCell:".bd ul",autoPage:true,effect:"top",autoPlay:true,vis:1});
  }
})